.skills {
  width: 80%;
  margin: auto;
  position: relative;
}

.lines {
    height: 100%;
    position: absolute;
    width: 100%;
    top: 0;
    list-style: none;
}

.line {
  height: inherit;
  width: 2px;
  position: absolute;
  background: rgba(238, 238, 238, 0.6);
}
.line.l--0 {
  left: 38px;
}
.line.l--25 {
  left: 25%;
}
.line.l--50 {
  left: 50%;
}
.line.l--75 {
  left: 75%;
}
.line.l--100 {
  left: calc(100% - 1px);
}

.line__label {
  display: block;
  width: 100px;
  text-align: center;
  position: absolute;
  bottom: -20px;    
  right: -25px;
  -webkit-transform: rotate(315deg);
          transform: rotate(315deg);
}
.line__label.title {
  text-transform: uppercase;
  font-weight: bold;
}

.charts {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
}

.chart {
  margin: 30px 0 0;
}
.chart:first-child {
  margin: 0;
}

.chart__title {
  display: block;
  margin: 0 0 10px;
  font-weight: bold;
  opacity: 0;
  -webkit-animation: 1s anim-lightspeed-in ease forwards;
          animation: 1s anim-lightspeed-in ease forwards;
}
.chart .chart__title {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}

.chart--horiz {
  overflow: hidden;
  list-style: none;
}

.chart__bar {
  height: 30px;
  margin-bottom: 10px;
  background: linear-gradient(to left, #4cb8c4, #3cd3ad);
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  opacity: 0;
  -webkit-animation: 1s anim-lightspeed-in ease forwards;
          animation: 1s anim-lightspeed-in ease forwards;
}

.chart__label {
  padding-left: 10px;
  line-height: 30px;
  color: white;
}

@-webkit-keyframes anim-lightspeed-in {
  0% {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes anim-lightspeed-in {
  0% {
    -webkit-transform: translateX(-200%);
            transform: translateX(-200%);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}


.timeline-wrapper {
    width: 100%;
}
.timeline {
    max-width: 100%;
    padding: 15px;
    position: relative;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.timeline:before {
    content: "";
    position: absolute;
    top: 0px;
    left: 33%;
    bottom: 0px;
    width: 0px;
    border: 2px solid;
}
.timeline:after {
    content: "";
    display: table;
    clear: both;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .timeline:before {
        left: calc(1% + 6px);
    }
}
.story-content {
    position: relative;
    margin-left: 30px;
    background: rgb(243, 243, 243);
    padding: 20px;
    border-radius: 8px;
    border:4px solid; 
}
.empty .story-content {
    background: transparent;
    box-shadow: none;
}
.story-content:after {
    content: "";
    display: table;
    clear: both;
}
.story-caption-container {
  position: absolute;
  top: -12px;
  left: 0;
  background: #ddd;
  padding: 1px;
  -webkit-clip-path: polygon(0% 0%, 95% 0%, 100% 50%, 95% 100%, 0% 100%);
  clip-path: polygon(0% 0%, 95% 0%, 100% 50%, 95% 100%, 0% 100%);
  height: 52px;
  box-sizing: border-box;
  width: calc(100% - 16px);
}
.story-caption {
    background: #e86971;
    color: #fff;
    padding: 0;
    font-size: 16px;
    font-weight: bold;
    margin: 0;
    border-right-color: transparent;
    -webkit-clip-path: polygon(0% 0%, 95% 0%, 100% 50%, 95% 100%, 0% 100%);
    clip-path: polygon(0% 0%, 95% 0%, 100% 50%, 95% 100%, 0% 100%);
    height: 50px;
    width: 100%;
    display: block;
    line-height: 52px;
    text-align: center;
}

.empty .story-content::before {
    display: none;
}
.story {
    clear: both;
    text-align: left;
    position: relative;
}
.timeline--animate .story .is-hidden {
    visibility: hidden;
}
.timeline--animate .story .bounce-in {
    visibility: visible;
    -webkit-animation: bounce-in 0.4s;
    animation: bounce-in 0.4s;
}
.story .story-caption-wrapper {
  margin-bottom: 0.5em;
  float: left;
  width: calc(33% + 6px);
  position: relative;
  height: 32px;
}
.story .story-caption-wrapper:before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  border: 4px solid;
  background-color: #ffffff;
  border-radius: 100%;
  top: 15%;
  right: 0;
  z-index: 99;
  box-sizing: content-box;
}
.story .story-continer {
  margin: 0 0 0.6em;
    float: right;
    width: 66%;
    color: #333;
}
.story .story-continer p {
    line-height: 1.4em;
}
.story .story-continer h1, .story .story-continer h2, .story .story-continer h3, .story .story-continer h4, .story .story-continer h5, .story .story-continer h6 {
    margin: 0;
}
.story .story-continer p:first-child {
    margin-top: 0;
    font-weight: 400;
}

@-webkit-keyframes bounce-in {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
    }
  
    100% {
      -webkit-transform: scale(1);
    }
}
@keyframes bounce-in {
    0% {
      opacity: 0;
      -webkit-transform: scale(0.5);
      transform: scale(0.5);
    }
  
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
}

.project-count {
    position: absolute;
    top: 20px;
    right: 20px;
    color: #fff;
    padding: 5px;
    border-radius: 50%;
    height: 20px;
    width: 20px;
    text-align: center;
    font-weight: bold;
    cursor: pointer;
}
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .story .story-caption-wrapper {
      float: left;
      width: 70%;
    }
    .story-caption-container {
      margin-left: 30px;
    }
    .story .story-caption-wrapper:before {
      top: 15%;
      left: 3px;
      right: auto;
      z-index: 99;
    }
    .story .story-continer {
      margin: 20px 0 3em;
      float: right;
      width: 99%;
    }
}

